import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ItemTransferUpdateForm from "./updateForm";
import ItemTransferDocUpdateForm from "./updateDocForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip, Tabs } from "antd";

// import theme from "../../../../settings/themes/themedefault";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/itemTransfer/actions";
import { Box } from "../../../App/app.style";

import HistoryItemTransfer from "./history";
import Print from "./print";
const TabPane = Tabs.TabPane;

const redirecTo = "/dashboard/inventories/item-transfers";

class UpdateItemTransfer extends Component {
  state = {
    visible: false
  };
  componentDidMount() {
    const { editItemTransfer } = this.props;
    const { id } = this.props.match.params;
    editItemTransfer(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.itemTransfer.error) {
      notification("error", this.props.itemTransfer.error);
      this.props.clearError();
    }

    if (this.props.itemTransfer.success) {
      // this.props.history.goBack();
      notification("success", this.props.itemTransfer.success);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateItemTransfer } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing itemTransfer code
    // value.customer_type_code = value.customer_type_code.toUpperCase();
    // console.log(value);
    updateItemTransfer(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelItemTransfer } = this.props;
    const { id } = this.props.match.params;

    cancelItemTransfer(id, value);
  };

  handleSubmitUnreceive = value => {
    const { unreceiveItemTransfer } = this.props;
    const { id } = this.props.match.params;

    unreceiveItemTransfer(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, loadingSubmit, itemTransfer, printData } = this.props.itemTransfer;
    const modified = {...itemTransfer, ...printData}

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="itemTransfers.update.title" />}>
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemTransfers.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryItemTransfer
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={itemTransfer ? itemTransfer.description : null}
          />
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              {loading && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <ItemTransferUpdateForm
                  submit={this.handleSubmit}
                  itemTransfer={this.props.itemTransfer}
                  handleSubmitCancel={this.handleSubmitCancel}
                  handleSubmitUnreceive={this.handleSubmitUnreceive}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
            >
              {loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <ItemTransferDocUpdateForm
                  submit={this.handleSubmit}
                  itemTransfer={this.props.itemTransfer}
                  params={this.props.match.params}
                />
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    itemTransfer: state.itemTransfer,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateItemTransfer);
