import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/itemTransfer/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Select,
  Divider,
  // Spin,
  Row,
  Col,
  // Button as ButtonAntd,
  Steps,
  // Empty,
  Tooltip,
  Icon,
  Popconfirm
} from "antd";

import EditableTable from "./editableTable";
import ModalTransaction from "./modal";
import ModalCancel from "./modalCancel";
import ModalUnreceive from "./modalUnreceive";
// import { Object } from "core-js";
import { customDot } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const Step = Steps.Step;

const {
  getItems,
  getTransactions,
  setPrintData,
  confirmItemTransfer,
  receiveItemTransfer,
  unreceiveItemTransfer
} = actions;

class ItemTransferUpdateForm extends Component {
  state = {
    id_from_location: null,
    id_to_location: null,
    item: null,
    items: [],
    itemDetails: {
      item_transfer_details: []
    },
    search: null,
    visible: false,
    visibleModalCancel: false,
    visibleModalUnreceive: false,
    clear: false,
    clearedItems: false,
    err: false,
    err_item: false
  };

  componentDidUpdate(prevProps, prevState) {
    const { itemTransfer } = this.props.itemTransfer;
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingForeign
    } = this.props.itemTransfer;
    const { itemDetails, clearedItems } = this.state;

    if (
      !clearedItems &&
      !loading &&
      !loadingSubmit &&
      !loadingItems &&
      !loadingTransactions &&
      !loadingForeign
    ) {
      const { getItems } = this.props;
      getItems(itemTransfer.id_from_location);
      this.setState({
        items: itemTransfer.item_transfer_details,
        id_from_location: itemTransfer.id_from_location,
        id_to_location: itemTransfer.id_to_location
      });
    }

    if (prevState.itemDetails.item_transfer_details !== itemDetails.item_transfer_details) {
      const { setPrintData } = this.props;
      setPrintData(itemDetails);
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { itemDetails } = this.state;

      // validasi item
      if (itemDetails.item_transfer_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const data = Object.assign({}, values, itemDetails);
      // console.log(data);
      if (!err) {
        this.setState({ err: false });
        this.props.submit(data);
      }
    });
  };

  handleSelectLocation = (name, id_location) => {
    const { getItems } = this.props;
    this.setState({
      [name]: id_location,
      clear:
        name === "id_to_location" && this.state.id_to_location !== id_location
          ? true
          : false
    });

    if (name === "id_from_location") {
      getItems(id_location);
    }
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSelectItem = value => {
    const { items } = this.props.itemTransfer;
    const { search } = this.state;
    const { getFieldValue } = this.props.form;
    const id_from_location = getFieldValue("id_from_location");
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      item: items.find(item => item.id_item === value)
    });

    if (search) {
      getItems(id_from_location);
      this.setState({ search: null });
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.itemTransfer;
    const { itemDetails } = this.state;
    const { setFieldsValue } = this.props.form;
    const { user } = this.props;
    let { default_employee_location } = user ? user : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];

    let transaction = transactions.find(
      curr_data => curr_data.id_item_request === value
    );

    const find = itemDetails.item_transfer_details.findIndex(
      currData => currData.id_item_request === transaction.id_item_request
    );

    if (find < 0) {
      transaction.item_request_details.map(
        data => (data.item_request_no = transaction.transaction_no)
      );
      this.setState({
        items: [
          ...itemDetails.item_transfer_details,
          ...transaction.item_request_details
        ],
        id_to_location: transaction.id_location,
        clear: false
      });
    }

    if (transaction.id_location === default_employee_location.id_location) {
      setFieldsValue({ 
        id_to_location: transaction.id_location,
        id_from_location: undefined
      });
    } else {
      setFieldsValue({ id_to_location: transaction.id_location });
    }
    this.handleModal();
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { getFieldValue } = this.props.form;
    const id_from_location = getFieldValue("id_from_location");
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_from_location) {
      getItems(id_from_location, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { getFieldsValue } = this.props.form;
      const { getTransactions } = this.props;

      let { transaction_date, id_to_location } = getFieldsValue([
        "transaction_date","id_to_location"
      ]);
      transaction_date = 
        moment(transaction_date.format("YYYY-MM-DD HH:mm:ss")).endOf("day").format("YYYY-MM-DD HH:mm:ss");
      const filter = `id_location=${id_to_location}&transaction_date=${transaction_date}`;
      getTransactions(1, null, filter);
    }
  }

  handleSaveItemList = value => {
    this.setState({
      itemDetails: {
        item_transfer_details: value
      },
      clear: false
    });
  };

  clearItem = value => {
    this.setState({
      item: null,
      itemDetails: {
        item_transfer_details: value
      },
      clear: false
    });
  };

  clearItems = value => {
    this.setState({
      items: [],
      itemDetails: {
        item_transfer_details: value
      },
      clear: false,
      clearedItems: true
    });
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };

  handleModalUnreceive = () => {
    this.setState({
      visibleModalUnreceive: !this.state.visibleModalUnreceive
    });
  };

  render() {
    // console.log(this.props.itemTransfer.transactions);
    const { visible } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { itemTransfer } = this.props;
    const {
      // items,
      fromLocations,
      toLocations,
      couriers,
      adjustmentTypes
    } = itemTransfer ? itemTransfer : [];
    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingForeign,
      loadingTransactions
    } = itemTransfer;
    const data = itemTransfer.itemTransfer ? itemTransfer.itemTransfer : [];

    // const used = data.is_used;
    // const error = itemTransfer.errorField ? itemTransfer.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const transactionStatus = () => {
      let curr = 0;
      switch (data.status) {
        case 1:
          curr = 0;
          break;
        case 2:
          curr = 1;
          break;
        case 4:
          curr = 2;
          break;
        case 3:
          curr = 3;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <Row style={{ margin: "12px 0px" }}>
          <Col span={16}>
            <Steps
              size="small"
              progressDot={customDot}
              current={transactionStatus()}
            >
              <Step title={getLanguage({ id: "pending" })} />
              <Step title={getLanguage({ id: "confirmed" })} />
              <Step title={getLanguage({ id: "received" })} />
              <Step title={getLanguage({ id: "cancelled" })} />
            </Steps>
          </Col>
        </Row>

        <ModalTransaction
          visible={visible}
          itemTransfer={itemTransfer}
          id_location={getFieldValue("id_to_location")}
          transaction_date={getFieldValue("transaction_date")
            ? moment(getFieldValue("transaction_date")).endOf("day").format("YYYY-MM-DD HH:mm:ss")
            : moment().endOf("day").format("YYYY-MM-DD HH:mm:ss")
          }
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loadingSubmit}
        />

        <ModalUnreceive
          visible={this.state.visibleModalUnreceive}
          handleModal={this.handleModalUnreceive}
          submit={this.props.handleSubmitUnreceive}
          loading={loadingSubmit}
        />

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.transactionNo" />}
                >
                  {getFieldDecorator("item_transfer_no", {
                    initialValue: data.item_transfer_no
                  })(<Input disabled={true} />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.transactionDate" />}
                  style={{ marginBottom: 0, paddingBottom: 0 }}
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: data.transaction_date
                      ? moment(data.transaction_date, "YYYY-MM-DD HH:mm:ss")
                      : null
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime={{ format: "HH:mm:ss" }}
                      format="DD MMMM YYYY HH:mm:ss"
                      disabled={
                        !permissions.includes("Can Back Date") ||
                        data.status !== 1
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.fromLocation" />}
                >
                  {getFieldDecorator("id_from_location", {
                    initialValue: data.id_from_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="itemTransfers.fromLocation" />
                      }
                      loading={loadingForeign}
                      onSelect={this.handleSelectLocation.bind(
                        this,
                        "id_from_location"
                      )}
                      disabled={data.status !== 1}
                      optionFilterProp="children"
                      filterOption={true}
                      allowClear
                    >
                      {fromLocations.map((data, key) =>
                        data.id_location !== getFieldValue("id_to_location") ? (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.toLocation" />}
                >
                  {getFieldDecorator("id_to_location", {
                    initialValue: data.id_to_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="itemTransfers.toLocation" />
                      }
                      loading={loadingForeign}
                      onSelect={this.handleSelectLocation.bind(
                        this,
                        "id_to_location"
                      )}
                      disabled={data.status !== 1}
                      optionFilterProp="children"
                      filterOption={true}
                      allowClear
                    >
                      {toLocations.map((data, key) =>
                        data.id_location !==
                        getFieldValue("id_from_location") ? (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="itemTransfers.note" />}
            >
              {getFieldDecorator("note", {
                initialValue: data.note
              })(
                <TextArea
                  disabled={data.status !== 1}
                  autosize={{ minRows: 4 }}
                />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.courier" />}
                >
                  {getFieldDecorator("id_courier", {
                    initialValue: data.id_courier
                  })(
                    <Select
                      showSearch
                      allowClear
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="itemTransfers.courier" />}
                      loading={loadingForeign}
                      disabled={data.status !== 1}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {couriers.map((data, key) => (
                        <Option key={key} value={data.id_courier}>
                          {`${data.courier_name} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.transferType" />}
                >
                  {getFieldDecorator("id_adjustment_type", {
                    initialValue: data.id_adjustment_type
                  })(
                    <Select
                      showSearch
                      allowClear
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="itemTransfers.transferType" />
                      }
                      loading={loadingForeign}
                      disabled={data.status !== 1}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {adjustmentTypes.map((data, key) => (
                        <Option key={key} value={data.id_adjustment_type}>
                          {`${data.adjustment_type_code} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <InputGroup>
              <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={<IntlMessages id="purchaseOrders.importRQ.help" />}
                    >
                      <IntlMessages id="purchaseOrders.importRQ" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  <Button
                    icon="select"
                    loading={loadingTransactions}
                    disabled={
                      loadingTransactions || !getFieldValue("id_to_location")
                    }
                    onClick={this.handleModal}
                    style={{ height: "31px", width: "100%" }}
                    disabled={data.status !== 1}
                  >
                    <IntlMessages id="button.import" />
                  </Button>
                </Form.Item>
              </Col>
              <Col span={16} />
            </InputGroup>
          </Col>
        </Row>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="itemTransfers.subtitle.itemList" />
        </Divider>

        {/* <Row>
          <Col span={24}>
            <Form.Item
              {...formItemLayout}
              extra={<IntlMessages id="itemTransfers.item.help" />}
              validateStatus={error.item_transfer_details ? "error" : null}
              help={
                error.item_transfer_details ? error.item_transfer_details : null
              }
            >
              <Select
                value={search ? search : []}
                showSearch
                allowClear
                // optionFilterProp="children"
                filterOption={false}
                placeholder={
                  <IntlMessages id="itemTransfers.item.placeholder" />
                }
                loading={loadingItems}
                notFoundContent={
                  loadingItems ? (
                    <Spin size="small" />
                  ) : getFieldValue("id_from_location") ? (
                    <Empty description={<IntlMessages id="noData" />} />
                  ) : (
                    <Empty
                      description={
                        <IntlMessages id="itemTransfers.item.noLocation" />
                      }
                    />
                  )
                }
                onSelect={this.handleSelectItem}
                onSearch={this.handleSearchItem}
                disabled={data.status !== 1}
              >
                {items.map(data => (
                  <Option key={data.id_item} value={data.id_item}>
                    {data.item_code + " - " + data.item_name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
        </Row> */}

        <Row>
          <Col span={24}>
            <EditableTable
              item={this.state.item}
              items={this.state.items}
              status={data.status}
              handleSaveItemList={this.handleSaveItemList}
              clearItem={this.clearItem}
              clearItems={this.clearItems}
              loadingTransactions={loadingTransactions}
              loadingItems={loadingItems}
              clear={this.state.clear}
            />
          </Col>
        </Row>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginRight: "5px", marginTop: "50px" }}
            loading={loading || loadingSubmit}
            disabled={
              data.status !== 1 || !permissions.includes("Update Item Transfer")
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Confirm */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            onConfirm={() =>
              this.props.confirmItemTransfer(data.id_item_transfer)
            }
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status !== 1 ||
                !permissions.includes("Confirm Item Transfer")
                  ? true
                  : false
              }
              loading={loading || loadingSubmit}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {fromLocations.findIndex(
            currData => currData.id_location === data.id_to_location
          ) !== -1 ? (
            data.status === 2 || data.status === 1 ? (
              <Popconfirm
                title={<IntlMessages id="confirmReceive" />}
                onConfirm={() =>
                  this.props.receiveItemTransfer(data.id_item_transfer)
                }
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Button
                  style={{ marginRight: "5px", padding: "0px 15px" }}
                  disabled={
                    data.status !== 2 ||
                    !permissions.includes("Confirm Item Transfer")
                      ? true
                      : false
                  }
                  loading={loading || loadingSubmit}
                >
                  <IntlMessages id="button.receiveTransaction" />
                </Button>
              </Popconfirm>
            ) : (
              <Button
                style={{ marginRight: "5px", padding: "0px 15px" }}
                disabled={
                  data.status !== 4 ||
                  !permissions.includes("Cancel Item Transfer")
                    ? true
                    : false
                }
                loading={loading || loadingSubmit}
                onClick={this.handleModalUnreceive}
              >
                <IntlMessages id="button.unreceiveTransaction" />
              </Button>
            )
          ) : null}
          {/* Receive */}

          {/* Cancel */}
          {fromLocations.findIndex(
            currData => currData.id_location === data.id_from_location
          ) !== -1 ? (
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status !== 2 ||
                !permissions.includes("Cancel Item Transfer")
              }
              loading={loading || loadingSubmit}
              onClick={this.handleModalCancel}
            >
              <IntlMessages id="button.cancelTransaction" />
            </Button>
          ) : null}

          <br />

          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}

          {this.state.err_item ? (
            <IntlMessages id="itemDataEmpty">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedItemTransferUpdateForm = Form.create({
  name: "update_item_transfer"
})(ItemTransferUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    {
      getItems,
      getTransactions,
      setPrintData,
      confirmItemTransfer,
      receiveItemTransfer,
      unreceiveItemTransfer
    }
  )
)(WrappedItemTransferUpdateForm);
